export default {
    data() {
        return {
            isPassword: true,
            isPassword1: true,
            newPwd: '',
            newPwd1: '',
            iscode: '',
            phone: ''
        };
    },
    onLoad(options) {
        this.iscode = options.code;
        this.phone = options.phone;
    },
    onBackPress(option) {
        plus.key.hideSoftKeybord();
    },
    methods: {
        newpassword: function(e) {
            this.newPwd = e.detail.value;
        },
        newpassword1: function(e) {
            this.newPwd1 = e.detail.value;
        },
        next() {
            var _this = this;
            var str = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/;
            if (!str.test(this.newPwd)) {
                uni.showToast({
                    title: '密码格式不正确',
                    icon: 'none',
                    duration: 2000
                });
            }
            if (this.newPwd == '') {
                uni.showToast({
                    title: '请输入新密码',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            if (this.newPwd1 == '') {
                uni.showToast({
                    title: '请确认密码',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            if (this.newPwd1 !== this.newPwd) {
                uni.showToast({
                    title: '两次密码不一致',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            var str = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/;
            if (!str.test(this.newPwd)) {
                uni.showToast({
                    title: '密码格式不正确',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            uni.request({
                url: this.url + 'users/forgot/change/',
                method: 'POST',
                data: {
                    mobile: this.phone,
                    password: this.newPwd,
                    sec_password: this.newPwd1,
                    code: this.iscode
                },
                header: {},
                success(res) {
                    uni.navigateTo({
                        url: '../login/login'
                    });
                    uni.showToast({
                        title: '密码找回成功',
                        icon: 'none',
                        duration: 2000
                    });
                }
            });
        }
    }
};
